import React from 'react'
import { Link } from 'react-router-dom';
import '../App.css'

const Home = () => {
    return (
        <div className = 'PlayTheGame'>
            TicTacBoom
            <div>
                <Link to='/game'>
                    <button className='GameButton'>Play</button>
                </Link>

                <Link to='/about'>
                    <button className='GameButton'>Help</button>
                </Link>
            </div>
        </div>
    )
}

export default Home
