import React from 'react'
import '../App.css'
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react';
import './cartas'
import cartas from './cartas';

const Game = () => {

    const [dice, setDice] = useState();
    const [card, setCard] = useState();
    const [bomb, setBomb] = useState();

    const rollDice = async () => {
        var randomNumber = Math.floor(Math.random() * (6 - 1 + 1)) + 1;
        console.log(randomNumber);
        let answer;
        switch (randomNumber) {
            case 1:
                answer = 'Tic'
                break;
            case 2:
                answer = 'Boom'
                break;
            case 3:
                answer = 'Tac'
                break;
            case 4:
                answer = 'Tac'
                break;
            case 5:
                answer = 'Boom'
                break;
            case 6:
                answer = 'Tic'
                break;
        }
        console.log(answer);
        setDice(answer);
    }

    const pickCard = async () => {
        console.log(cartas)
        let answer = cartas[Math.floor(Math.random() * cartas.length)];
        console.log(answer);
        setCard(answer);
    }

    const Bomb = async () => {
        let answer = Math.floor(Math.random() * (120 - 15 + 1)) + 15;
        setBomb(answer);
    }



    return (
        <div className='PlayTheGame'>
            Play the Game

            <div className='GameButtonDivs'>
                <button className='GameButton' onClick={rollDice} >Roll the dice</button>
                <button className='GameButton' onClick={pickCard} >Pick a Card</button>
                <button className='GameButton' onClick={Bomb} >Start the bomb</button>
            </div>
            <div className='PlayTheGame'>
                <label className = 'labelsGame' ><b>Dice</b>: {dice}</label>
                <label className = 'labelsGame' ><b>Card</b>: {card}</label>
                <label className = 'labelsGame' ><b>Bomb</b>: {bomb}</label>
            </div>
            <div>
                <Link to='/'>
                    <button className='backbutton'>Back</button>
                </Link>
            </div>
        </div>

    )
}

export default Game
